export class LazyNumberSequence {
    constructor(start, end, step) {
        this.__figpad_lazy_seq__ = "n";
        if (start > end) {
            this.length = end === Infinity ? Infinity : Math.ceil(Math.max(0, start - end) / step);
            this.step = -step;
        }
        else {
            this.length = end === Infinity ? Infinity : Math.ceil(Math.max(0, end - start) / step);
            this.step = step;
        }
        this.start = start;
        this.end = end;
    }
    [Symbol.iterator]() {
        let value = this.start, end = this.end, step = this.step;
        if (this.start < this.end) {
            return {
                next() {
                    if (value >= end) {
                        return { done: true, value: 0 };
                    }
                    let v = value;
                    value += step;
                    return { value: v, done: false };
                }
            };
        }
        else {
            return {
                next() {
                    if (value <= end) {
                        return { done: true, value: 0 };
                    }
                    let v = value;
                    value += step;
                    return { value: v, done: false };
                }
            };
        }
    }
    map(f) {
        if (this.end === Infinity) {
            throw new Error("infinite sequence");
        }
        let a = [];
        if (this.start < this.end) {
            for (let i = 0, v = this.start; v < this.end; v += this.step) {
                a.push(f(v, i++));
            }
        }
        else {
            for (let i = 0, v = this.start; v > this.end; v += this.step) {
                a.push(f(v, i++));
            }
        }
        return a;
    }
    array() {
        if (this.end === Infinity) {
            throw new Error("infinite sequence");
        }
        let a = [];
        if (this.start < this.end) {
            for (let v = this.start; v < this.end; v += this.step) {
                a.push(v);
            }
        }
        else {
            for (let v = this.start; v > this.end; v += this.step) {
                a.push(v);
            }
        }
        return a;
    }
    toString() {
        let glue = ", ";
        if (this.end === Infinity) {
            let s = "";
            let i = 0;
            for (let val of this) {
                if (i > 0) {
                    if (i > 50) {
                        s += " ... ∞";
                        break;
                    }
                    s += glue;
                }
                s += val;
                i++;
            }
            return s;
        }
        return this.join(glue);
    }
    join(glue) {
        let s = "";
        let i = 0;
        if (this.end === Infinity) {
            throw new Error("infinite sequence");
        }
        else {
            for (let val of this) {
                if (i > 0) {
                    s += glue;
                }
                s += val;
                i++;
            }
        }
        return s;
    }
    at(index) {
        return this.start + (this.step * index);
    }
}
