    updateHiddenAreas() {
        // Sad hack to work around isolating scripts.
        // Many many hours went into finding a better solution than this.
        // Ultimately this is the simples and most reliable approach. Monaco synchronizes
        // state with the TS worker, so simply patching the code in the TS worker would cause
        // the editor to go bananas.
        //
        // IMPORTANT: This function MUST NOT MODIFY THE MODEL CONTENTS.
        //            If it does, it would cause an infinite loop.
        //            If this is ever needed, change onDidChangeModelContent as appropriate.
        //
        if (DEBUG) {
            if (!this.editor.setHiddenAreas) {
                console.error("MONACO issue: editor.setHiddenAreas not found!");
            }
        }
        let hiddenAreas = [];
        this._lineNumberOffset = 0;
        if (!this._currentScript.isROLib) {
            let model = this._currentModel;
            let lastLine = model.getLineCount();
            hiddenAreas = [
                // Range(startLineNumber, startColumn, endLineNumber, endColumn)
                new monaco.Range(1, 1, 1, 1),
                new monaco.Range(lastLine, 1, lastLine, 1),
            ];
            this._lineNumberOffset = 1;
        }
        ;
        this.hiddenAreas = hiddenAreas; // since readonly for outside
        if (!DEBUG_HIDDEN_AREAS) {
            ;
            this.editor.setHiddenAreas(hiddenAreas);
        }
        this.adjustSelection();
    }
    // makes sure there is a line break after the last line; an extra blank line at the end
    // for view zones, like print output on last line
    ensureTrailingNewline() {
        const lastVisibleLineno = this.currentModel.getLineCount() - 1;
        const lastVisibleLineContent = this.currentModel.getLineContent(lastVisibleLineno);
        if (lastVisibleLineContent.trim().length > 0) {
            console.log({ lastVisibleLineContent });
            // add linebreak after last visible line.
            // save & restore view state to avoid persisting temporary scroll & selection changes
            const viewState = this.editor.saveViewState();
            this.currentModel.applyEdits([{
                    range: new monaco.Range(lastVisibleLineno, Infinity, lastVisibleLineno, Infinity),
                    text: "\n",
                    forceMoveMarkers: false,
                }]);
            this.editor.restoreViewState(viewState);
        }
    }
