import { db } from "./data";
import { EventEmitter } from "./event";
import { dlog } from "./util";
import { WindowSize } from "../common/messages";
class Data {
    constructor() {
        // values define new-user defaults
        //
        // function value means that on load, the actual stored value is passed through the function.
        // if said function returns undefined, the values is removed from local storage.
        //
        //                    DEFAULT VALUE
        this.lastOpenScript = 0; // DEPRECATED(lastOpenScriptGUID)
        this.lastOpenScriptGUID = "";
        this.editorViewState = null;
        this.menuVisible = false;
        this.uiScale = 1;
        this.showLineNumbers = false;
        this.wordWrap = true;
        this.monospaceFont = true;
        // codeFolding           :bool = false  // do not enable! messes with hiddenAreas
        this.showWhitespace = false;
        this.indentGuides = false;
        this.minimap = false;
        this.quickSuggestions = true;
        this.quickSuggestionsDelay = 500; // ms
        this.hoverCards = true;
        this.fontLigatures = false;
        this.windowSize = [WindowSize.MEDIUM, WindowSize.MEDIUM];
        this.useTabs = true; // TAB for indentation instead of space?
        this.tabSize = 2;
        // -------------------------------------------------
        // Deprecated data properties.
        //
        // When stored data for one of these properties is discovered on load, the corresponding
        // function is called _after_ all non-deprecated data values has been read.
        // The function can inspect and modify `data` to migrate information.
        this.fontSize = (data, value) => {
            // convert fontSize to uiScale
            data.uiScale = (value > 22 ? 2 :
                value > 18 ? 1.75 :
                    value > 16 ? 1.5 :
                        value > 15 ? 1.4 :
                            value > 13 ? 1.3 :
                                value > 12 ? 1.2 :
                                    value > 11 ? 1.1 :
                                        1);
        };
    }
}
class Config extends EventEmitter {
    constructor() {
        super(...arguments);
        this._dirtyProps = new Set();
        this._saveTimer = null;
        this.version = 0; // increments (locally, in memory) whenever changes are made
        this.data = new Data();
    }
    // -----------------------------------------------------------------
    // Data properties
    get editorViewState() {
        return this.data.editorViewState;
    }
    set editorViewState(v) {
        this._set("editorViewState", v);
    }
    get lastOpenScript() { return this.data.lastOpenScript; }
    set lastOpenScript(v) { if (v != 0) {
        this._set("lastOpenScript", v);
    } }
    get lastOpenScriptGUID() { return this.data.lastOpenScriptGUID; }
    set lastOpenScriptGUID(v) { this._set("lastOpenScriptGUID", v); }
    // @DEPRECATED -> uiScale
    get uiScale() { return this.data.uiScale; }
    set uiScale(v) { this._set("uiScale", v); }
    get menuVisible() { return this.data.menuVisible; }
    set menuVisible(v) { this._set("menuVisible", v); }
    get showLineNumbers() { return this.data.showLineNumbers; }
    set showLineNumbers(v) { this._set("showLineNumbers", v); }
    get wordWrap() { return this.data.wordWrap; }
    set wordWrap(v) { this._set("wordWrap", v); }
    get monospaceFont() { return this.data.monospaceFont; }
    set monospaceFont(v) { this._set("monospaceFont", v); }
    // get codeFolding() :bool { return this.data.codeFolding }
    // set codeFolding(v :bool) { this._set("codeFolding", v) }
    get showWhitespace() { return this.data.showWhitespace; }
    set showWhitespace(v) { this._set("showWhitespace", v); }
    get indentGuides() { return this.data.indentGuides; }
    set indentGuides(v) { this._set("indentGuides", v); }
    get minimap() { return this.data.minimap; }
    set minimap(v) { this._set("minimap", v); }
    get quickSuggestions() { return this.data.quickSuggestions; }
    set quickSuggestions(v) { this._set("quickSuggestions", v); }
    get quickSuggestionsDelay() { return this.data.quickSuggestionsDelay; }
    set quickSuggestionsDelay(v) { this._set("quickSuggestionsDelay", v); }
    get hoverCards() { return this.data.hoverCards; }
    set hoverCards(v) { this._set("hoverCards", v); }
    get fontLigatures() { return this.data.fontLigatures; }
    set fontLigatures(v) { this._set("fontLigatures", v); }
    get windowSize() { return this.data.windowSize; }
    set windowSize(v) { this._set("windowSize", v); }
    get useTabs() { return this.data.useTabs; }
    set useTabs(v) { this._set("useTabs", v); }
    get tabSize() { return this.data.tabSize; }
    set tabSize(v) { this._set("tabSize", v); }
    // -----------------------------------------------------------------
    _set(key, value) {
        if (this.data[key] !== value) {
            ;
            this.data[key] = value;
            this.dirty(key);
        }
    }
    dirty(prop) {
        // dlog("config dirty", prop)
        this._dirtyProps.add(prop);
        if (this._saveTimer === null) {
            this._saveTimer = setTimeout(() => { this.saveDirty(); }, 200);
        }
        this.version++;
        this.triggerEvent("change", { key: prop });
    }
    async load() {
        let keys = Object.keys(this.data);
        let migrate = [];
        await db.read(["config"], ...keys.map(k => 
        // callback function for each value
        (s) => s.get(k).then(v => {
            let handler = this.data[k];
            if (handler && typeof handler == "function") {
                if (v !== undefined) {
                    migrate.push([k, () => handler(this.data, v)]);
                }
            }
            else {
                this.data[k] = v === undefined ? this.data[k] : v;
            }
        })));
        // migrate old data?
        if (migrate.length > 0) {
            let keys = migrate.map(e => e[0]);
            let rmkeys = [];
            console.log(`[figpad] migrating config keys ${keys}`);
            for (let [key, fn] of migrate) {
                try {
                    let value = fn();
                    if (value === undefined) {
                        rmkeys.push(key);
                    }
                    else {
                        this.data[key] = value;
                    }
                }
                catch (err) {
                    console.warn(`[figpad] migration for config "${key}" failed: ${err.stack | err}`);
                }
            }
            // clear deprecated data from db
            if (rmkeys.length > 0) {
                await db.modify(["config"], async (s) => {
                    for (let k of rmkeys) {
                        s.delete(k);
                    }
                });
            }
        }
    }
    async saveDirty() {
        let dirtyProps = Array.from(this._dirtyProps);
        this._dirtyProps.clear();
        return this.saveProps(dirtyProps);
    }
    async saveAll() {
        return this.saveProps(Object.keys(this.data));
    }
    async saveProps(props) {
        clearTimeout(this._saveTimer);
        this._saveTimer = null;
        let values = props.map(k => this.data[k]); // copy since put is async
        dlog("[config] save", props, values);
        await db.modify(["config"], async (s) => {
            props.forEach((k, i) => s.put(values[i], k));
        });
    }
}
export const config = new Config();
