export class EventEmitter {
    constructor() {
        this._events = new Map();
    }
    addListener(e, handler) {
        let s = this._events.get(e);
        if (s) {
            s.add(handler);
        }
        else {
            this._events.set(e, new Set([handler]));
        }
    }
    on(e, handler) {
        return this.addListener(e, handler);
    }
    removeListener(e, handler) {
        let s = this._events.get(e);
        if (s) {
            s.delete(handler);
            if (s.size == 0) {
                this._events.delete(e);
            }
        }
    }
    removeListeners(e) {
        this._events.delete(e);
    }
    removeAllListeners() {
        this._events.clear();
    }
    triggerEvent(e, data) {
        let s = this._events.get(e);
        if (s)
            for (let handler of s) {
                handler(data);
            }
    }
}
EventEmitter.prototype.on = EventEmitter.prototype.addListener;
